import apiFetch from "@wordpress/api-fetch"
import { Button, PanelBody, PanelRow } from "@wordpress/components"
import { useBlockProps, InnerBlocks, InspectorControls, MediaUpload, MediaUploadCheck } from "@wordpress/block-editor"
import { registerBlockType } from "@wordpress/blocks"
import { useEffect } from "@wordpress/element"

const TEMPLATE = [ [ 'voblocktheme/columns', { templateLock: 'all' }, [
	[ 'core/column', { templateLock: 'all' }, [
		[ 'core/heading' ], ['core/paragraph', { className: 'large', level: 2 } ],
	] ],
	[ 'core/column', { templateLock: 'all' }, [
		[ 'voblocktheme/listitem' ], [ 'voblocktheme/listitem' ],
	] ],
  [ 'core/column', { templateLock: 'all' }, [
		[ 'voblocktheme/listitem' ], [ 'voblocktheme/listitem' ],
	] ],
] ] ]


registerBlockType("voblocktheme/intro3column", {
  title: "Intro w/ 3 Columns",
  supports: {
    align: ["full"]
  },
  attributes: {
    align: { type: "string", default: "full" },
    imgID: { type: "number" },
    imgURL: { type: "string", default: intro3column.fallbackimage },
    title: { type: "string" },
    body: { type: "string" }
  },
  edit: EditComponent,
  save: SaveComponent
})

function EditComponent(props) {
  useEffect(
    function () {
      if (props.attributes.imgID) {
        async function go() {
          const response = await apiFetch({
            path: `/wp/v2/media/${props.attributes.imgID}`,
            method: "GET"
          })
          props.setAttributes({ imgURL: response.media_details.sizes.large.source_url })
        }
        go()
      }
    },
    [props.attributes.imgID]
  )

  function updateTitle(e) {
    props.setAttributes({ title: e.target.value })
  }

  function updateBody(e) {
    props.setAttributes({ body: e.target.value })
  }

  function onFileSelect(x) {
    props.setAttributes({ imgID: x.id })
  }

  return (
    <>
      <InspectorControls>
        <PanelBody title="Background" initialOpen={true}>
          <PanelRow>
            <MediaUploadCheck>
              <MediaUpload
                onSelect={onFileSelect}
                value={props.attributes.imgID}
                render={({ open }) => {
                  return <Button onClick={open}>Choose Image</Button>
                }}
              />
            </MediaUploadCheck>
          </PanelRow>
        </PanelBody>
      </InspectorControls>

      <section class="welcome sec-with-imgbg">
        <div class="container" style={{ margin: "35px" }}>
          <div class="row">
            <div class="col-sm-12 col-lg-4">
              <div class="welcome-txt">
                <input type="text" placeholder="Title" className="h3" value={props.attributes.title} onChange={updateTitle} />
                <textarea placeholder="Body Text" className="large" value={props.attributes.body} onChange={updateBody} />
              </div>
            </div>
            <InnerBlocks allowedBlocks={["voblocktheme/columns"]} />
          </div>
        </div>
        <img src={props.attributes.imgURL} class="sec-imgbg welcome-imgbg" />
      </section>
    </>
  )
}

function SaveComponent() {
  return <InnerBlocks.Content />
}